<template>
  <div class="login">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="用户名：" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="密码：" prop="pass">
                <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
            </el-form-item>
        </el-form>
  </div>
</template>

<script>
import { ElButton, ElForm, ElFormItem, ElInput, ElMessage } from 'element-plus'
export default {
    name: "Login",
    components: {
        ElButton, ElForm, ElFormItem, ElInput, ElMessage
    },
    data() {
        return {
            ruleForm: {
                name: '',
                pass: ''
            },
            rules: {
                name: [
                    { required: true, message: '请输入用户名', trigger: 'blur' }
                ],
                pass: [
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        submitForm(name){
            this.$refs[name].validate((valid) => {
                if (valid) {
                    if(this.ruleForm.name === "yh" && this.ruleForm.pass === "stt"){
                        sessionStorage.setItem("userInfo", JSON.stringify({ name: this.ruleForm.name, pass: this.ruleForm.pass }));
                        this.$router.replace("/");
                    }else{
                        ElMessage.warning("用户名或密码错误！");
                    }
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    }
}
</script>

<style lang="less" scoped>
.login{
    position: absolute;
    left: 50%;
    top: 50%;
    width: 500px;
    transform: translate(-50%, -50%);
}
</style>